// System configuration options for currency and timezone
// All user-facing text must use i18n translation keys

export interface Currency {
  code: string
  nameKey: string // i18n translation key for currency name
  symbol: string
  decimals: number
}

export interface Timezone {
  value: string
  labelKey: string // i18n translation key for timezone label
  offset: string
  region: string // i18n translation key for region
}

