// Group timezones by region for organized display
export const getTimezonesByRegion = () => {
  const grouped: Record<string, Timezone[]> = {}
  timezones.forEach((tz) => {
    if (!grouped[tz.region]) {
      grouped[tz.region] = []
    }
    grouped[tz.region].push(tz)
  })
  return grouped
}
